<template>
  <div
    class="picker-item"
    @touchstart="_onTouchStart"
    @touchmove.prevent="_onTouchMove"
    @touchend="_onTouchEnd"
    @touchcancel="_onTouchEnd"
  >
    <div class="picker-mask" :style="pickerMask"></div>
    <ul class="picker-li" :style="transformStyle">
      <li
        v-for="(item, index) in data"
        v-text="timeFormat(item.name||item)"
        :key="index"
        :class="{'disabled':item.disabled}"
      ></li>
    </ul>
  </div>
</template>
<script type="text/ecmascript-6">
import dayjs from "dayjs";
import "dayjs/locale/zh-cn";

export default {
  name: "picker-item",
  data() {
    return {
      startY: 0, //touch
      startOffset: 0, //touch
      offset: 0 //
    };
  },
  watch: {
    height() {
      //mountedheight
      this._moveTo();
    },
    data() {
      //
      this._moveTo();
    }
  },
  props: {
    height: Number, //
    data: Array,
    change: Function,
    value: String, //
    index: Number //change
  },
  components: {},
  methods: {
    _getTouch(event) {
      return event.changedTouches[0] || event.touches[0];
    },
    _getVisibleCount() {
      //
      return Math.floor(this.$parent.visibleCount / 2);
    },
    _onTouchStart(event) {
      const touch = this._getTouch(event);
      this.startOffset = this.offset;
      this.startY = touch.clientY;
    },
    _onTouchMove(event) {
      const touch = this._getTouch(event);
      const currentY = touch.clientY;
      const distance = currentY - this.startY;
      this.offset = this.startOffset + distance;
    },
    _onTouchEnd() {
      let index = Math.round(this.offset / this.$parent.liHeight);
      const vc = this._getVisibleCount();
      // console.log("liHeight:" + this.$parent.liHeight);
      // console.log("this.offset:" + this.offset);
      // console.log("index:" + index);
      // index
      const indexMax = vc - this.data.length;

      // console.log(index, this.data, this.data.length, indexMax, vc);

      if (index >= vc) {
        index = 0; // 
      } else if (index < indexMax) {
        // 
        index = this.data.length - 1; //
      } else {
        index = vc - index;
      }
      /*if (index >= vc) {
          index = 0;// 
        } else if (Math.abs(index) + vc > this.data.length - 1) {
          index = this.data.length - 1;//
        } else {
          if (index <= 0) {
            index = vc - index;
          }
        }*/
      // console.log("index2:" + index);
      this._setIndex(index, true);
    },
    _setIndex(index, bool) {
      //5301li*2
      //(5/2index)*liHeight
      //disabled
      //
      if (index > this.data.length - 1) {
        index = this.data.length - 1;
      }

      if (this.data.length > 0) {
        bool ? (index = this._isDisabled(index, index)) : "";
        this.offset = (this._getVisibleCount() - index) * this.height;
        //
        const value = this.data[index].value || this.data[index];
        this.change ? this.change(value, this.index, bool) : "";
      }
    },
    _isDisabled(index, index2) {
      if (this.data[index].disabled) {
        if (index == this.data.length - 1) {
          index = -1; //
        }
        //disabled
        if (index + 1 == index2) {
          return index2;
        }
        return this._isDisabled(index + 1);
      }
      return index;
    },
    _moveTo() {
      //value
      let index = 0;
      for (let i = 0; i < this.data.length; i++) {
        let v = this.data[i].value || this.data[i];
        if (this.value === v) {
          index = i;
          break;
        }
      }
      // index = this.data[i].findIndex(e=>e===this.value)
      this._setIndex(index, false);
      //valueindex=0
    },

    // 
    timeFormat(date) {
      let time = date;
      if (date.length > 5) {
        time = dayjs(date)
          .locale("zh-cn")
          .format("YYYYMMDD dddd");

        const today = dayjs()
          .locale("zh-cn")
          .format("YYYYMMDD dddd");
        const todayWeek = dayjs()
          .locale("zh-cn")
          .format("dddd");

        time = time === today ? time.replace(todayWeek, "") : time; // 
        time = time.replace("", ""); // 
      }

      return time;
    }
  },
  computed: {
    pickerMask() {
      return {
        //1
        backgroundSize: "100% " + this._getVisibleCount() * this.height + "px"
      };
    },
    transformStyle() {
      return {
        transition: "all 150ms ease",
        transform: `translate3d(0, ${this.offset}px, 0)`
      };
    }
  },
  mounted() {},
  filters: {}
};
</script>

<style lang="scss" scoped>
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
}

.picker-item {
  width: 100%;
  position: relative;
  overflow: hidden;
  // 
  &:nth-child(2) {
    width: 250%;
  }
  .picker-mask {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    z-index: 3;
    background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.95),
        rgba(255, 255, 255, 0.6)
      ),
      linear-gradient(0deg, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
    background-repeat: no-repeat;
    background-position: top, bottom;
  }
  li {
    height: 50px;
    line-height: 50px;
    text-align: center;
    overflow: hidden;
    box-sizing: border-box;
    &.disabled {
      font-style: italic;
    }
  }
}
</style>