<template>
  <div class="picker">
    <transition name="fade">
      <div class="mask" v-show="visible" @click="handleCancel"></div>
    </transition>
    <transition name="slide">
      <div class="picker-content" v-show="visible" ref="content">
        <!--  -->
        <div class="picker-control">
          <a
            href="javascript:void(0);"
            class="picker-cancel"
            v-text="cancelText"
            @click="handleCancel"
          ></a>
          <span v-text="titleText" v-if="titleText" class="picker-title"></span>
          <a
            href="javascript:void(0);"
            class="picker-confirm"
            v-text="confirmText"
            @click="handleConfirm"
          ></a>
        </div>
        <!--  -->
        <div class="picker-group" :style="{ height: visibleCount * liHeight + 'px' }">
          <div class="picker-border"></div>
          <pickerItem
            v-for="(item,index) in data"
            :data="item.value"
            :key="index"
            :index="index"
            :height="liHeight"
            :change="handelChange"
            :value="valueArr?valueArr[index]:''"
            ref="item"
          ></pickerItem>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
import dayjs from "dayjs";
import pickerItem from "./picker-item";

export default {
  name: "DatePickerScroll",
  data() {
    return {
      data: this.getData(),
      valueArr: this.getTime(this.value),
      liHeight: 0,
      newValue: this.value
    };
  },
  watch: {
    visible(v) {
      // 
      if (v && this.liHeight == 0) {
        this.getDisplayHeight();
      }
    }
  },
  props: {
    visible: { type: Boolean, default: false }, //  
    cancelText: { type: String, default: "" }, //  
    confirmText: { type: String, default: "" }, //  
    titleText: { type: String, default: "" }, //  
    startDate: String, //  10 2019-11-11
    endDate: String, //  10 2019-11-12 
    visibleCount: { type: Number, default: 5 }, //  5
    value: { type: Date, default: new Date() }, //  :
    onCancel: Function, // 
    onConfirm: Function, // 
    onChange: Function // 
  },
  components: {
    pickerItem
  },
  methods: {
    // 
    handleCancel(e) {
      let item = this.$refs.item;
      for (let i in item) {
        item[i]._moveTo();
      }
      this.$emit("update:visible", false);
      this.onCancel ? this.onCancel(this.value) : "";
      e.stopPropagation();
    },
    // 
    handleConfirm(e) {
      this.$emit("update:visible", false);
      this.onConfirm
        ? this.onConfirm(this.newValue)
        : this.$emit("input", this.newValue);
      e.stopPropagation();
    },
    // 
    handelChange(value, index, bool) {
      //
      if (typeof this.value == "string") {
        //this.$emit('input', value);
        this.newValue = value;
      } else {
        this.valueArr[index] = value;
        //vuethis.value
        //this.value
        //newValue.splice(index, 1, value);//
        //this.$emit('input', newValue);
        this.newValue = this.restoreTime(this.valueArr);
      }
      //bool=false
      if (bool) {
        this.onChange ? this.onChange(value, index) : "";
      }
    },
    // 
    getDisplayHeight() {
      //
      const obj = this.$refs.content;
      const clone = obj.cloneNode(true);
      clone.style.display = "block";
      clone.style.position = "absolute";
      clone.style.opacity = 0;
      clone.style.top = "-10000px";
      obj.parentNode.appendChild(clone);
      const li = clone.querySelector("li");
      if (li) {
        //this.liHeight = li.offsetHeight;//
        this.liHeight = parseFloat(window.getComputedStyle(li, null).height); //
      }
      obj.parentNode.removeChild(clone);
    },

    //  count 24/60
    generateTime(count) {
      let timeArr = [];
      for (let i = 0; i <= count - 1; i++) {
        const t = i < 10 ? "0" + i.toString() : i.toString();
        timeArr.push(t);
      }
      return timeArr;
    },

    // 
    generateDate(startDate, endDate) {
      startDate = dayjs(startDate);
      endDate = dayjs(endDate);
      let dateArr = [];
      const count = endDate.diff(startDate, "days"); // 
      for (let i = count; i >= 0; i--) {
        const t = dayjs(endDate)
          .subtract(i, "days")
          .toISOString();
        dateArr.push(t);
      }
      return dateArr;
    },

    //  2019-11-11 -> IOSstring
    timeFormat(date) {
      return dayjs(date)
        .startOf("day")
        .toISOString();
    },

    // data     
    getData() {
      const hours = this.generateTime(24);
      const minutes = this.generateTime(60);

      const startDate = this.startDate
        ? this.timeFormat(this.startDate)
        : dayjs()
            .startOf("day")
            .subtract(10, "days")
            .toISOString();
      const endDate = this.endDate
        ? this.timeFormat(this.endDate)
        : dayjs()
            .startOf("day")
            .subtract(10, "days")
            .toISOString();

      const dates = this.generateDate(startDate, endDate);
      return [
        {
          value: dates
        },
        {
          value: hours
        },
        {
          value: minutes
        }
      ];
    },

    // value       
    getTime(value) {
      const date = dayjs(value)
        .startOf("day")
        .toISOString();
      const hour = dayjs(value).format("HH");
      const minute = dayjs(value).format("mm");
      return [date, hour, minute];
    },

    // value       
    restoreTime(valueArr) {
      const date = dayjs(valueArr[0]).format("YYYY-MM-DD");
      const hour = valueArr[1];
      const minute = valueArr[2];
      const all = `${date} ${hour}:${minute}`;
      return dayjs(all, "YYYY-MM-DD HH:mm").toDate();
    }
  },
  computed: {},
  mounted() {
    this.getDisplayHeight();
  },
  filters: {}
};
</script>

<style lang="scss" scoped>
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
}
a {
  color: #666;
  text-decoration: none;
}
