    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 100;
  }
  .picker-content {
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    background: #fff;
    z-index: 101;
  }
  .picker-control {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    padding: 0 20px;
    box-sizing: border-box;
    .picker-title {
      display: block;
      flex: 2;
      text-align: center;
      font-size: 16px;
    }
    a {
      display: block;
      &:last-child {
        text-align: right;
        color: #04ab02;
      }
    }
  }
  .picker-group {
    display: flex;
    position: relative;
    overflow: hidden;
    .picker-border {
      left: 0;
      top: 50%;
      position: absolute;
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #e5e5e5;
      border-top: 1px solid #e5e5e5;
      width: 100%;
      box-sizing: border-box;
      transform: translateY(-50%);
    }
  }
}
.fade-enter-active {
  animation: fadeIn 0.5s;
}
.fade-leave-active {
  animation: fadeOut 0.5s;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.slide-enter-active {
  animation: fadeUp 0.5s;
}
.slide-leave-active {
  animation: fadeDown 0.5s;
}
@keyframes fadeUp {
  0% {
    opacity: 0.6;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeDown {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0.6;
    transform: translateY(100%);
  }
}
</style>